@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonial{
  background-color: v.$verde;
  font-size: 2.4rem;
  padding: 2rem;
  color: v.$blanco;
  border-radius: 2rem;

  blockquote{
    padding-left: 5rem;
    position: relative;

    &::before{
      content: '';
      background-image: url(../img/comilla.svg);
      background-repeat: no-repeat;
      display: block;
      width: 6rem;
      height: 8rem;
      position: absolute;
      left: -2rem;
  }
}

p{
  text-align: right;
  color: v.$blanco;
}
}